.description {
	width: 100%;
	max-width: 100%;
}

.description__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	display: grid;
    grid-template-columns: 50% 1fr;
	padding-top: 149px;
}

.description__title {
	font-weight: 500;
    font-size: 17px;
    line-height: 25px;
    letter-spacing: 2px;
    color: rgba(255, 121, 76, 1);
    margin-bottom: 11px;
}

.description__par {
	font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: -1px;
    color: rgba(29, 38, 58, 1);
    margin-bottom: 19px;
}

.description__text {
	font-size: 17px;
    line-height: 30px;
    letter-spacing: -0.5px;
    color: rgba(29, 38, 58, 1);
    width: 85%;
    margin-bottom: 40px;
}

.description__link {
	font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: rgba(29, 38, 58, 1);
    text-decoration: none;
    background-color: rgba(255, 205, 40, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 168px;
    height: 64px;
    border-radius: 8px;
    cursor: pointer;
}

.description__desc {
	font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: rgba(255, 121, 76, 1);
    width: 113px;
    height: 48px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    background-color: rgba(255, 255, 255, 1);
}

.description__card {
	line-height: 0;
    display: inline-block;
}

.description__media {
	display: grid;
	grid-template-columns: 300px 300px;
	grid-template-rows: 100px 100px 100px 100px 100px;
	grid-gap: 25px;
    padding-left: 14px;
}

.description__card:nth-child(1) {
    grid-column: 1/2;
    grid-row: 2/5;
}

.description__card:nth-child(2) {
	grid-column: 2/3;
	grid-row: 1/4;
}

.description__card:nth-child(3) {
	grid-column: 2/3;
	grid-row: 4/-1;
}

.description__content {
	padding-left: 81px;
	padding-top: 135px;
}

