.page-header {
	width: 100%;
	max-width: 100%;
}

.page-header__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr 4fr 2fr;
	padding-top: 26px;
	padding-bottom: 26px;
}

.page-header__link {
	font-weight: 500;
	font-size: 17px;
	line-height: 25px;
	color: rgba(29, 38, 58, 1);
	text-decoration: none;
	letter-spacing: -0.5px;
}

.page-header__block-btn {
	padding-left: 140px;
}

.page-header__btn {
	width: 151px;
	height: 48px;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: -0.005em;
	font-weight: 500;
	background-color: rgba(29, 38, 58, 1);
	color: rgba(255, 255, 255, 1);
	border: none;
	cursor: pointer;
	box-shadow: 0px 30px 60px rgba(195, 207, 217, 0.3);
	border-radius: 8px;
}

.page-header__btn:hover {
	background-color: rgba(29, 38, 58, 0.7);
}

.page-header__block-logo {
	padding-left: 81px;
	padding-top: 13px;
}

.page-header__list {
	display: flex;
	flex-wrap: wrap;
}

.page-header__nav {
	padding-left: 63px;
	padding-top: 12px;
}

.page-header__item:nth-child(1) {
	margin-right: 60px;
}

.page-header__item:nth-child(2) {
	margin-right: 61px;
}

.page-header__item:nth-child(3) {
	margin-right: 61px;
}

.page-header__item:nth-child(4) {
	margin-right: 61px;
}