.species {
	width: 100%;
	max-width: 100%;
}

.species__wrapper {
	max-width: 1300px;
	margin: 0 auto;
    background-color: rgba(246, 248, 251, 1);
    padding-top: 42px;
    border-radius: 8px;
    display: grid;
    grid-template-columns: 1fr;
}

.species__block-title {
    padding-left: 80px;
}

.species__title {
    font-size: 17px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 2px;
    color: rgba(255, 121, 76, 1);
    margin-bottom: 11px;
}

.species__par {
    font-size: 48px;
    font-weight: 600;
    line-height: 58px;
    letter-spacing: -1px;
    width: 50%;
    margin-bottom: 19px;
}

.species__text {
    font-size: 17px;
    line-height: 30px;
    letter-spacing: -0.5px;
    width: 35%;
    margin-bottom: 80px;
}

.species__caption {
    font-size: 18px;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    margin-bottom: 15px;
}

.species__item-text {
    font-size: 16px;
    font-style: normal;
    line-height: 26px;
    letter-spacing: -0.005em;
    color: rgba(96, 103, 117, 1);
    width: 90%;
    margin-bottom: 26px;
}

.species__item:nth-child(2) .species__item-text {
    width: 95%;
}

.species__btn {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0em;
    color: rgba(255, 121, 76, 1);
    border: none;
    width: 120px;
    text-align: left;
    background-color: transparent;
    cursor: pointer;
    position: relative;
}
.species__btn:hover {
    font-size: 17px;
}

.species__btn img {
    position: absolute;
    right: 4px;
    top: 6px;
}

.species__block-img {
	background: rgba(240, 242, 245, 1) url("../img/imou-cam.png") no-repeat 48% 50%;
	width: 341px;
	height: 450px;
    border-radius: 8px;
    margin-bottom: 24px;
}

.species__item:nth-child(2) .species__block-img {
    width: 342px;
    background: rgba(240, 242, 245, 1) url("../img/ring-cam.png") no-repeat 52% 52%;
}

.species__item:nth-child(3) .species__block-img {
    width: 338px;
    background: rgba(240, 242, 245, 1) url("../img/ezviz-cam.png") no-repeat 60% 55%;
}

.species__list {
    display: flex;
    flex-wrap: wrap;
    background: linear-gradient(to bottom, rgba(246, 248, 251, 1) 37%, rgba(255, 255, 255, 1) 37%);
    padding-left: 110px;
}

.species__item {
    width: min-content;
}

.species__item:nth-child(1),
.species__item:nth-child(2) {
    margin-right: 30px;
}

.species__item-content {
    padding-left: 17px;
}

.species__item:nth-child(2) .species__item-content,
.species__item:nth-child(3) .species__item-content {
    padding-left: 20px;
}

.species__list-wrapper {
    position: relative;
}

.species__swiper {
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    border: 1px solid rgba(223, 223, 223, 0.7);
    background-color: rgba(255, 255, 255, 1);
    cursor: pointer;
    padding-top: 3px;
}

.species__swiper:nth-child(1) {
    top: 200px;
    left: 80px;
    box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.2);
}

.species__swiper:nth-of-type(2) {
    right: 79px;
    top: 200px;
    box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.3);
    border: none;
    background-color: rgba(255, 205, 40, 1);
}