.news {
	width: 100%;
	max-width: 100%;
	margin-bottom: 151px;
}

.news__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	background: linear-gradient(to bottom, #F9FAFB 67%, #FFFFFF 67%);
	padding-top: 72px;
}

.news__position {
	padding: 0 66px 0 80px;
}

.news__par {
	font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: -1px;
    width: 70%;
}

.news__text {
	font-size: 17px;
    line-height: 30px;
    letter-spacing: -0.5px;
    color: rgba(96, 103, 117, 1);
    width: 85%;
}

.news__btn {
	width: 60px;
	height: 60px;
	border-radius: 8px;
	border: none;
	background-color: rgba(255, 205, 40, 1);
	margin-right: 12px;
	cursor: pointer;
}

.news__btn:nth-child(2) {
	background-color: rgba(255, 255, 255, 1);
	border: 1px solid rgba(223, 223, 223, 0.7);
	margin-right: 0;
}

.news__subject {
	font-weight: 600;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 11px;
}

.news__item-text {
	font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.005em;
    color: rgba(96, 103, 117, 1);
    width: 87%;
}

.news__event {
	font-size: 14px;
    line-height: 21px;
    color: rgba(96, 103, 117, 1);
    display: flex;
    padding: 0 48px 0 20px;
}

.news__data {
	font-weight: 500;
	color: rgba(255, 121, 76, 1);
	margin-left: auto;
}

.news__content {
	display: grid;
	grid-template-columns: 49% 1fr auto;
	margin-bottom: 79px;
}

.news__list {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	
}

.news__item {
	width: 360px;
	background-color: rgba(255, 255, 255, 1);
	border-radius: 8px;
	box-shadow: 0px 18px 35px rgba(31, 31, 38, 0.05);
	padding-bottom: 25px;
}

.news__item:nth-child(1),
.news__item:nth-child(2) {
	margin-right: 30px;
}

.news__item:nth-child(3) .news__wrap-img {
	margin-bottom: 16px;
}

.news__item:nth-child(3) .news__wrap-text {
	padding-left: 23px;
	margin-bottom: 32px;
}

.news__block-btn {
	justify-self: end;
	padding-top: 27px;
}

.news__block-text {
	padding-top: 31px;
}

.news__wrap-img {
	padding-left: 12px;
	padding-top: 12px;
	margin-bottom: 14px;
}

.news__wrap-img img {
	border-radius: 8px;
}

.news__wrap-text {
	padding-left: 20px;
	margin-bottom: 34px;
}