.page-footer {
	width: 100%;
	max-width: 100%;
}

.page-footer__wrapper {
	max-width: 1300px;
	margin: 0 auto;
    padding-top: 89px;
    padding-bottom: 57px;
	background-color: rgba(29, 38, 58, 1);
	border-radius: 8px;
}

.help {
	display: flex;
    justify-content: space-between;
    padding: 0 135px 0 83px;
    margin-bottom: 148px;
}

.help__title {
	font-size: 18px;
    line-height: 27px;
    font-weight: 400;
    color: rgba(255, 121, 76, 1);
    letter-spacing: 0.02em;
    margin-bottom: 9px;
}

.help__text {
	font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    color: rgba(255, 255, 255, 1);
    letter-spacing: -1px;
}

.help__btn {
	font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    background-color: rgba(255, 205, 40, 1);
    border-radius: 8px;
    border: none;
    width: 166px;
    min-height: 56px;
    cursor: pointer;
}

.help__btn:hover {
	opacity: 0.9;
}

.help__btn:active {
	opacity: 0.8;
}

.help__mail {
	width: 166px;
	min-height: 56px;
	border: none;
    background-color: transparent;
}

.help__mail[placeholder] {
	font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: rgba(73, 78, 89, 1);
    letter-spacing: -0.01em;
}

.help__wrapper-mail {
    padding-top: 9px;
    width: 42%;
}

.help__wrap-mail {
	border: 1px solid #363A42;
	border-radius: 8px;
	max-width: 455px;
	height: 76px;
    padding-left: 30px; 
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.service {
    padding: 0 82px;
    margin-bottom: 100px;
}

.service__wrapper {
    display: flex;
}

.service__text {
	font-size: 17px;
    line-height: 30px;
    color: rgba(149, 160, 186, 1);
    letter-spacing: -0.5px;
    opacity: 0.9;
    margin-bottom: 12px;
}

.service__subject {
	font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: rgba(255, 255, 255, 1);
    letter-spacing: -0.2px;
    margin-bottom: 30px;
}

.service__par {
	font-size: 16px;
    line-height: 24px;
    color: rgba(149, 160, 186, 1);
    opacity: 0.9;
}

.service__par:nth-of-type(1),
.service__par:nth-of-type(2) {
    margin-bottom: 24px;
}

.service__content:nth-of-type(1) {
    width: 21%;
    margin-right: 119px;
}

.service__content:nth-of-type(2) {
    width: 21%;
    margin-right: 62px;
}

.service__content:nth-of-type(3) {
    margin-right: 122px;
}

.service__content:nth-of-type(4) .service__par {
    padding-left: 33px;
}

.service__content:nth-of-type(4) .service__par {
    position: relative;
}

.service__mail::before {
    content: "";
    position: absolute;
    left: 1px;
    top: 4px;
    background-image: url("../img/mail.svg");
    width: 18px;
    height: 13px;
}

.service__tel::before {
    content: "";
    position: absolute;
    background-image: url("../img/tel.svg");
    left: 0;
    top: 4px;
    width: 15px;
    height: 15px;
}

.service__loc::before {
    content: "";
    position: absolute;
    background-image: url("../img/location.svg");
    width: 13px;
    height: 19px;
    left: 0;
    top: 3px;
}

.service__content img {
    margin-bottom: 40px;
    cursor: pointer;
}

.service__content a {
    text-decoration: none;
    color: inherit;
}

.service__list {
    display: flex;
}

.service__item {
    width: 30px;
    height: 30px;
}

.service__item:nth-child(1),
.service__item:nth-child(2) {
    margin-right: 5px;
}

.service__item:nth-child(1) .service__social-link {
    background: url(../img/facebook.svg) no-repeat 50%;
    width: 30px;
    height: 30px;
}

.service__item:nth-child(2) .service__social-link {
    background: url(../img/twitter.svg) no-repeat 50%;
    width: 30px;
    height: 30px;
}

.service__item:nth-child(3) .service__social-link {
    background: url(../img/insta.svg) no-repeat 50%;
    width: 30px;
    height: 30px;
}

.service__social-link {
    display: block;
}

.copy {
    padding-top: 53px;
    position: relative;
}

.copy::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 22px;
    margin: 0 auto;
    height: 1px;
    width: 88%;
    background-color: rgba(54, 58, 66, 1);
}

.copy__text {
	font-size: 14px;
    line-height: 19px;
    color: rgba(96, 103, 117, 1);
    text-align: center;
}