.character {
	width: 100%;
	max-width: 100%;
	position: relative;
}

.character__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	background-color: rgba(246, 248, 251, 1);
	padding-top: 150px;
	padding-bottom: 150px;
	display: grid;
	grid-template-columns: 1fr 2fr;
}

.character__block-image {
	padding-left: 79px;
    position: absolute;
}

.character__title {
	font-weight: 500;
    font-size: 17px;
    line-height: 25px;
    color: rgba(255, 121, 76, 1);
    letter-spacing: 2px;
    margin-bottom: 11px;
}

.character__subtitle {
	font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    color: rgba(29, 38, 58, 1);
    letter-spacing: -1px;
    margin-bottom: 19px;
}

.character__par {
	font-weight: 400;
    font-size: 17px;
    line-height: 30px;
    letter-spacing: -0.5px;
    color: rgba(29, 38, 58, 1);
    width: 65%;
}

.character__text {
	font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: rgba(29, 38, 58, 1);
}

.character__item {
	font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    letter-spacing: -0.005em;
    color: rgba(29, 38, 58, 1);
}

.character__item:nth-child(1) {
	margin-bottom: 8px;
}

.character__item:nth-child(2) {
	margin-bottom: 8px;
}

.character__item::before {
	content: "";
	position: absolute;
	left: 0;
	top: 7px;
	width: 7px;
	height: 7px;
	border-radius: 50%;
	background-color: rgba(255, 121, 76, 1);
}

.character__card:nth-child(2) .character__item::before {
	background-color: rgba(23, 135, 251, 1);
	width: 8px;
	height: 8px;
}

.character__item:nth-child(2)::before {
	top: 39px;
}

.character__item:nth-child(3)::before {
	top: 71px;
}

.character__block-img {
	display: flex;
	margin-bottom: 25px;
}

.character__block-card {
	display: flex;
	flex-wrap: wrap;
}

.character__card {
	background-color: rgba(255, 255, 255, 1);
	box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.1);
	padding: 32px 35px 31px 60px;
    border-radius: 8px;
    margin-right: 50px;
}

.character__card:nth-child(2) {
	margin-right: 0;
	padding-right: 66px;
}

.character__list {
	list-style: none;
	position: relative;
	padding-left: 15px;
}

.character__card:nth-child(2) .character__list {
	padding-left: 16px;
}

.character__content {
	padding-left: 123px;
	padding-top: 1px;
	position: relative;
	grid-column-start: 2;
}

.character__content-text {
	padding-left: 186px;
	margin-bottom: 90px;
}

.character__pic-block {
	width: 40px;
	height: 40px;
	background-color: rgba(255, 121, 76, 0.08);
	border-radius: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 16px;
}

.character__card:nth-child(2) .character__pic-block {
	background-color: rgba(23, 135, 251, 0.1);
}

.character__pic-text {
	padding-top: 9px;
}