.benefits {
	width: 100%;
	max-width: 100%;
	padding-top: 97px;
	margin-bottom: 150px;
}

.benefits__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	border: 1.2px solid #F2F4F7;
	border-radius: 8px;
	padding: 70px 0;
}

.benefits__par {
	font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: -1px;
    margin-bottom: 17px;
}

.benefits__text {
	font-size: 17px;
    line-height: 30px;
    letter-spacing: -0.5px;
    padding-top: 2px;
}

.benefits__subject {
	font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -1px;
    margin-bottom: 11px;
}

.benefits__item-text {
	font-size: 16px;
    line-height: 26px;
    color: rgba(96, 103, 117, 1);
    letter-spacing: -0.005em;
    width: 87%;
    margin-bottom: 48px;
}

.benefits__link {
	font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
    color: rgba(29, 38, 58, 1);
    position: relative;
    width: 110px;
    display: block;
}

.benefits__link img {
	position: absolute;
	right: -9px;
	top: 5px;
}

.benefits__element-img {
	width: 65px;
	height: 65px;
	background-color: rgba(255, 255, 255, 1);
	position: absolute;
	box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.15);
    border-radius: 8px;
    display: flex;
    justify-content: center;
    left: 30px;
    top: -20px;
}

.benefits__content {
	display: flex;
	margin-bottom: 72px;
}

.benefits__block-btn {
	margin-left: auto;
}

.benefits__btn {
	width: 67px;
	height: 60px;
	box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.2);
    border-radius: 8px;
    border: 1px solid rgba(223, 223, 223, 0.7);
    background-color: rgba(255, 255, 255, 1);
    margin-right: 18px;
    padding-top: 5px;
    cursor: pointer;
}

.benefits__btn:nth-child(2) {
	border: none;
	background-color: rgba(255, 205, 40, 1);
	margin-right: 0;
}

.benefits__list {
	display: flex;
	padding-left: 2px;
}

.benefits__position {
	padding: 0 80px 0 80px;
}

.benefits__item {
	width: 350px;
	height: 300px;
	background-color: rgba(240, 242, 245, 1);
	box-shadow: 0px 40px 100px rgba(216, 216, 216, 0.2);
    border-radius: 8px;
    margin-right: 45px;
    position: relative;
    padding-left: 27px;
}

.benefits__item:nth-child(2) {
	background-color: rgba(255, 205, 40, 1);
}

.benefits__item:last-child {
	margin-right: 0;
}

.benefits__item-position {
	padding-top: 95px;
}

.benefits__item-content {
	padding-left: 3px;
}