.indicator {
	width: 100%;
	max-width: 100%;
}

.indicator__wrapper {
	max-width: 1300px;
	margin: 0 auto;
	padding-top: 50px;
	padding-bottom: 69px;
	background-color: #1D263A;
    border-radius: 8px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.indicator__title {
	font-weight: bold;
    font-size: 72px;
    line-height: 89px;
    letter-spacing: -1px;
    color: #FFFFFF;
    margin: 0 0 21px 0;
}

.indicator__elem {
	color: #FFCD28;
}

.indicator__text {
	font-weight: 400;
	font-size: 18px;
    line-height: 32px;
    letter-spacing: -1px;
    color: rgba(214, 214, 214, 1);
    width: 80%;
    margin-bottom: 40px;
}

.indicator__btn {
	width: 168px;
	height: 64px;
	background-color: rgba(255, 205, 40, 1);
	border: none;
	border-radius: 8px;
	font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: rgba(29, 38, 58, 1);
    margin-bottom: 61px;
    cursor: pointer;
}

.indicator__btn:hover {
	opacity: 0.9;
}

.indicator__par {
	font-weight: 500;
    font-size: 48px;
    line-height: 72px;
    letter-spacing: -1px;
    color: #FFFFFF;
    margin-bottom: 7px;
}

.indicator__plus {
	color: #FFCD28;
}

.indicator__desc {
	font-weight: 400;
    font-size: 16px;
    letter-spacing: -1px;
    color: rgba(149, 160, 186, 1);
}

.indicator__list {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 99%;
}

.indicator__content {
	padding-left: 80px;
	padding-top: 49px;
}

.indicator__block-img {
	padding-left: 27px;
}