* {
  margin: 0;
  padding: 0;
}
.page {
  height: 100%;
}
.page-body {
  font-family: "Poppins", "Arial", "sans-serif";
  font-weight: 400;
  color: #1d263a;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  min-height: 100%;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 100%;
  max-width: 100%;
}
.page-header__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 4fr 2fr;
  padding-top: 26px;
  padding-bottom: 26px;
}
.page-header__link {
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  color: #1d263a;
  text-decoration: none;
  letter-spacing: -0.5px;
}
.page-header__block-btn {
  padding-left: 140px;
}
.page-header__btn {
  width: 151px;
  height: 48px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.005em;
  font-weight: 500;
  background-color: #1d263a;
  color: #ffffff;
  border: none;
  cursor: pointer;
  box-shadow: 0px 30px 60px rgba(195, 207, 217, 0.3);
  border-radius: 8px;
}
.page-header__btn:hover {
  background-color: rgba(29, 38, 58, 0.7);
}
.page-header__block-logo {
  padding-left: 81px;
  padding-top: 13px;
}
.page-header__list {
  display: flex;
  flex-wrap: wrap;
}
.page-header__nav {
  padding-left: 63px;
  padding-top: 12px;
}
.page-header__item:nth-child(1) {
  margin-right: 60px;
}
.page-header__item:nth-child(2) {
  margin-right: 61px;
}
.page-header__item:nth-child(3) {
  margin-right: 61px;
}
.page-header__item:nth-child(4) {
  margin-right: 61px;
}
.indicator {
  width: 100%;
  max-width: 100%;
}
.indicator__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 69px;
  background-color: #1D263A;
  border-radius: 8px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.indicator__title {
  font-weight: bold;
  font-size: 72px;
  line-height: 89px;
  letter-spacing: -1px;
  color: #FFFFFF;
  margin: 0 0 21px 0;
}
.indicator__elem {
  color: #FFCD28;
}
.indicator__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: -1px;
  color: #d6d6d6;
  width: 80%;
  margin-bottom: 40px;
}
.indicator__btn {
  width: 168px;
  height: 64px;
  background-color: #ffcd28;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: #1d263a;
  margin-bottom: 61px;
  cursor: pointer;
}
.indicator__btn:hover {
  opacity: 0.9;
}
.indicator__par {
  font-weight: 500;
  font-size: 48px;
  line-height: 72px;
  letter-spacing: -1px;
  color: #FFFFFF;
  margin-bottom: 7px;
}
.indicator__plus {
  color: #FFCD28;
}
.indicator__desc {
  font-weight: 400;
  font-size: 16px;
  letter-spacing: -1px;
  color: #95a0ba;
}
.indicator__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 99%;
}
.indicator__content {
  padding-left: 80px;
  padding-top: 49px;
}
.indicator__block-img {
  padding-left: 27px;
}
.sidebar {
  width: 100%;
  max-width: 100%;
}
.sidebar__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding-top: 80px;
}
.sidebar-position {
  margin-bottom: 150px;
}
.sidebar__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 155px;
  border: 1px solid #F2F4F7;
  padding-top: 69px;
  padding-bottom: 63px;
}
.sidebar__item {
  margin-right: 62px;
}
.character {
  width: 100%;
  max-width: 100%;
  position: relative;
}
.character__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  background-color: #f6f8fb;
  padding-top: 150px;
  padding-bottom: 150px;
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.character__block-image {
  padding-left: 79px;
  position: absolute;
}
.character__title {
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  color: #ff794c;
  letter-spacing: 2px;
  margin-bottom: 11px;
}
.character__subtitle {
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  color: #1d263a;
  letter-spacing: -1px;
  margin-bottom: 19px;
}
.character__par {
  font-weight: 400;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: -0.5px;
  color: #1d263a;
  width: 65%;
}
.character__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #1d263a;
}
.character__item {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  letter-spacing: -0.005em;
  color: #1d263a;
}
.character__item:nth-child(1) {
  margin-bottom: 8px;
}
.character__item:nth-child(2) {
  margin-bottom: 8px;
}
.character__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #ff794c;
}
.character__card:nth-child(2) .character__item::before {
  background-color: #1787fb;
  width: 8px;
  height: 8px;
}
.character__item:nth-child(2)::before {
  top: 39px;
}
.character__item:nth-child(3)::before {
  top: 71px;
}
.character__block-img {
  display: flex;
  margin-bottom: 25px;
}
.character__block-card {
  display: flex;
  flex-wrap: wrap;
}
.character__card {
  background-color: #ffffff;
  box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.1);
  padding: 32px 35px 31px 60px;
  border-radius: 8px;
  margin-right: 50px;
}
.character__card:nth-child(2) {
  margin-right: 0;
  padding-right: 66px;
}
.character__list {
  list-style: none;
  position: relative;
  padding-left: 15px;
}
.character__card:nth-child(2) .character__list {
  padding-left: 16px;
}
.character__content {
  padding-left: 123px;
  padding-top: 1px;
  position: relative;
  grid-column-start: 2;
}
.character__content-text {
  padding-left: 186px;
  margin-bottom: 90px;
}
.character__pic-block {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 121, 76, 0.08);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
}
.character__card:nth-child(2) .character__pic-block {
  background-color: rgba(23, 135, 251, 0.1);
}
.character__pic-text {
  padding-top: 9px;
}
.species {
  width: 100%;
  max-width: 100%;
}
.species__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  background-color: #f6f8fb;
  padding-top: 42px;
  border-radius: 8px;
  display: grid;
  grid-template-columns: 1fr;
}
.species__block-title {
  padding-left: 80px;
}
.species__title {
  font-size: 17px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 2px;
  color: #ff794c;
  margin-bottom: 11px;
}
.species__par {
  font-size: 48px;
  font-weight: 600;
  line-height: 58px;
  letter-spacing: -1px;
  width: 50%;
  margin-bottom: 19px;
}
.species__text {
  font-size: 17px;
  line-height: 30px;
  letter-spacing: -0.5px;
  width: 35%;
  margin-bottom: 80px;
}
.species__caption {
  font-size: 18px;
  font-weight: 600;
  line-height: 27px;
  letter-spacing: 0em;
  margin-bottom: 15px;
}
.species__item-text {
  font-size: 16px;
  font-style: normal;
  line-height: 26px;
  letter-spacing: -0.005em;
  color: #606775;
  width: 90%;
  margin-bottom: 26px;
}
.species__item:nth-child(2) .species__item-text {
  width: 95%;
}
.species__btn {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  color: #ff794c;
  border: none;
  width: 120px;
  text-align: left;
  background-color: transparent;
  cursor: pointer;
  position: relative;
}
.species__btn:hover {
  font-size: 17px;
}
.species__btn img {
  position: absolute;
  right: 4px;
  top: 6px;
}
.species__block-img {
  background: #f0f2f5 url("../img/imou-cam.png") no-repeat 48% 50%;
  width: 341px;
  height: 450px;
  border-radius: 8px;
  margin-bottom: 24px;
}
.species__item:nth-child(2) .species__block-img {
  width: 342px;
  background: #f0f2f5 url("../img/ring-cam.png") no-repeat 52% 52%;
}
.species__item:nth-child(3) .species__block-img {
  width: 338px;
  background: #f0f2f5 url("../img/ezviz-cam.png") no-repeat 60% 55%;
}
.species__list {
  display: flex;
  flex-wrap: wrap;
  background: linear-gradient(to bottom, #f6f8fb 37%, #ffffff 37%);
  padding-left: 110px;
}
.species__item {
  width: min-content;
}
.species__item:nth-child(1),
.species__item:nth-child(2) {
  margin-right: 30px;
}
.species__item-content {
  padding-left: 17px;
}
.species__item:nth-child(2) .species__item-content,
.species__item:nth-child(3) .species__item-content {
  padding-left: 20px;
}
.species__list-wrapper {
  position: relative;
}
.species__swiper {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 8px;
  border: 1px solid rgba(223, 223, 223, 0.7);
  background-color: #ffffff;
  cursor: pointer;
  padding-top: 3px;
}
.species__swiper:nth-child(1) {
  top: 200px;
  left: 80px;
  box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.2);
}
.species__swiper:nth-of-type(2) {
  right: 79px;
  top: 200px;
  box-shadow: 0px 30px 60px rgba(195, 206, 217, 0.3);
  border: none;
  background-color: #ffcd28;
}
.description {
  width: 100%;
  max-width: 100%;
}
.description__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 50% 1fr;
  padding-top: 149px;
}
.description__title {
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  letter-spacing: 2px;
  color: #ff794c;
  margin-bottom: 11px;
}
.description__par {
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: -1px;
  color: #1d263a;
  margin-bottom: 19px;
}
.description__text {
  font-size: 17px;
  line-height: 30px;
  letter-spacing: -0.5px;
  color: #1d263a;
  width: 85%;
  margin-bottom: 40px;
}
.description__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: #1d263a;
  text-decoration: none;
  background-color: #ffcd28;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 168px;
  height: 64px;
  border-radius: 8px;
  cursor: pointer;
}
.description__desc {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: #ff794c;
  width: 113px;
  height: 48px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: #ffffff;
}
.description__card {
  line-height: 0;
  display: inline-block;
}
.description__media {
  display: grid;
  grid-template-columns: 300px 300px;
  grid-template-rows: 100px 100px 100px 100px 100px;
  grid-gap: 25px;
  padding-left: 14px;
}
.description__card:nth-child(1) {
  grid-column: 0.5;
  grid-row: 0.4;
}
.description__card:nth-child(2) {
  grid-column: 0.66666667;
  grid-row: 0.25;
}
.description__card:nth-child(3) {
  grid-column: 0.66666667;
  grid-row: -4;
}
.description__content {
  padding-left: 81px;
  padding-top: 135px;
}
