}
.benefits__item-position {
  padding-top: 95px;
}
.benefits__item-content {
  padding-left: 3px;
}
.news {
  width: 100%;
  max-width: 100%;
  margin-bottom: 151px;
}
.news__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  background: linear-gradient(to bottom, #F9FAFB 67%, #FFFFFF 67%);
