}
.news__par {
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: -1px;
  width: 70%;
}
.news__text {
  font-size: 17px;
  line-height: 30px;
  letter-spacing: -0.5px;
  color: #606775;
  width: 85%;
}
.news__btn {
  width: 60px;
  height: 60px;
  border-radius: 8px;
