}
.news__subject {
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 11px;
}
.news__item-text {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.005em;
  color: #606775;
  width: 87%;
}
.news__event {
  font-size: 14px;
  line-height: 21px;
