}
.news__wrap-text {
  padding-left: 20px;
  margin-bottom: 34px;
}
.page-footer {
  width: 100%;
  max-width: 100%;
}
.page-footer__wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding-top: 89px;
  padding-bottom: 57px;
  background-color: #1d263a;
