  margin-bottom: 100px;
}
.service__wrapper {
  display: flex;
}
.service__text {
  font-size: 17px;
  line-height: 30px;
  color: #95a0ba;
  letter-spacing: -0.5px;
  opacity: 0.9;
  margin-bottom: 12px;
}
.service__subject {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #ffffff;
  letter-spacing: -0.2px;
  margin-bottom: 30px;
}
.service__par {
