.service__content:nth-of-type(4) .service__par {
  position: relative;
}
.service__mail::before {
  content: "";
  position: absolute;
  left: 1px;
  top: 4px;
  background-image: url("../img/mail.svg");
  width: 18px;
  height: 13px;
}
.service__tel::before {
  content: "";
  position: absolute;
  background-image: url("../img/tel.svg");
  left: 0;
  top: 4px;
  width: 15px;
  height: 15px;
}
.service__loc::before {
  content: "";
  position: absolute;
  background-image: url("../img/location.svg");
  width: 13px;
  height: 19px;
  left: 0;
  top: 3px;
}
.service__content img {
  margin-bottom: 40px;
  cursor: pointer;
}
.service__content a {
  text-decoration: none;
  color: inherit;
}
.service__list {
  display: flex;
}
.service__item {
  width: 30px;
  height: 30px;
}
.service__item:nth-child(1),
.service__item:nth-child(2) {
  margin-right: 5px;
}
.service__item:nth-child(1) .service__social-link {
  background: url(../img/facebook.svg) no-repeat 50%;
  width: 30px;
  height: 30px;
}
.service__item:nth-child(2) .service__social-link {
  background: url(../img/twitter.svg) no-repeat 50%;
  width: 30px;
  height: 30px;
}
.service__item:nth-child(3) .service__social-link {
  background: url(../img/insta.svg) no-repeat 50%;
  width: 30px;
  height: 30px;
}
.service__social-link {
  display: block;
}
.copy {
  padding-top: 53px;
  position: relative;
}
.copy::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 22px;
  margin: 0 auto;
  height: 1px;
  width: 88%;
  background-color: #363a42;
}
.copy__text {
  font-size: 14px;
  line-height: 19px;
  color: #606775;
  text-align: center;
}
